

// 1. Utilities
// --------------

@mixin clearfix() {
    &:before,
    &:after {
        content: "";
        display: table;
    }
    &:after {
        clear: both;
    }
}

@mixin truncateText($overflow: ellipsis){
  overflow: hidden;
  white-space: nowrap;
  text-overflow: $overflow; // values are: clip, ellipsis, or a string
}


// 2. Breakpoints
// --------------

@mixin bp-small {
  @media (min-width: #{$tablet-width}) and (max-width: #{$desktop-width - 1px}) {
    @content;
  }
}

@mixin bp-medium {
  @media (min-width: #{$desktop-width}) {
    @content;
  }
}

@mixin bp-large {
  @media (min-width: #{$wide-width}) {
    @content;
  }
}




// 3. Typography
// --------------

@function calculateRem($size) {
  $remSize: $size / $base-font-size;
  @return #{$remSize}rem;
}

@mixin font-size($size) {
  font-size: $size + px; //Fallback in px
  font-size: calculateRem($size);
  line-height: ($size / $base-font-size) * 1.5;
}



@mixin center-block {
   display: block;
   margin-left: auto;
   margin-right: auto;
 }


